import React from 'react';

const Card = ({ name, email, id }) => {
  return (
    <div className='tc grow br3 pa3 ma2 bg-light-green dib bw2 shadow-5'>
      <div>
        <h2>{name}</h2>
        <p>email: {email}</p>
      </div>
      <img alt='robots pictures' src={`https://robohash.org/${id}`} />
    </div>
  );
}

export default Card;



